import React, {Component} from 'react'

export default class ErrorDialog extends Component {
    render() {
        let className = 'modal';

        if (this.props.showErrorDialog) {
            className += ' is-active';
        }

        return (
            <div className={className}>
                <div className="modal-background"></div>
                <div className="modal-card">
                    <header className="modal-card-head">
                        <p className="modal-card-title">Ошибка EAISTO</p>
                        <button className="delete" aria-label="close" onClick={this.props.toggleShowErrorDialog}></button>
                    </header>
                    <section className="modal-card-body">
                        {this.props.errorDialogMessage}
                    </section>
                    <footer className="modal-card-foot">
                        <button className="button" onClick={this.props.toggleShowErrorDialog}>Закрыть</button>
                    </footer>
                </div>
            </div>
        )
    }
}


