import React, {Component} from 'react'

export default class ConfirmationModal extends Component {
    render() {
        let className = 'modal';

        if (this.props.showConfirmationModal) {
            className += ' is-active';
        }

        return (
            <div className={className}>
                <div className="modal-background"></div>
                <div className="modal-card">
                    <header className="modal-card-head">
                        <p className="modal-card-title">Подтверждение действия</p>
                        <button className="delete" aria-label="close" onClick={this.props.toggleShowConfirmationModal}></button>
                    </header>
                    <section className="modal-card-body">
                        {this.props.confirmationModalMessage}
                    </section>
                    <footer className="modal-card-foot">
                        <button className="button is-success" onClick={this.props.onConfirm}>Подтвердить</button>
                        <button className="button" onClick={this.props.toggleShowConfirmationModal}>Отмена</button>
                    </footer>
                </div>
            </div>
        )
    }
}


